import Vue from 'vue'
import Vuex from 'vuex'
import firebase from "@/firebase"

Vue.use(Vuex)

const db = firebase.database

export default new Vuex.Store({
  state: {
    items: {},
  },
  getters: {
    items: state => state.items,
    item: state => id => state.items[id]
  },
  mutations: {
    setItems: function (state, data) {
      state.items = data
    }
  },
  actions: {
    add: function(context, payload) {
      db.ref('items/').push(payload.data)
      .then(_ => {
        payload.vm.$snotify.success('Item added', 'All right!');
      })
      .catch( error => {
        payload.vm.$snotify.error(error, 'Error');
        console.error(error)
      })
    },
    change: function(context, payload) {
      const index = payload.index
      const data = payload.data
      db.ref('items/' + index).update(data)
        .then(_ => {
          payload.vm.$snotify.success( 'Item changed', 'All right!');
        })
        .catch( error => {
          payload.vm.$snotify.error(error, 'Error');
          console.error(error)
        })
    },
    remove: function(context, payload) {
      db.ref('items/' + payload.index).remove()
      .then(_ => {
        payload.vm.$snotify.success('Item removed', 'All right!');
      })
      .catch( error => {
        payload.vm.$snotify.error(error, 'Error');
        console.error(error)
      })
    },
    getFirebaseData: function(context, payload) {
      db.ref('items').on('value', function(snapshot) {
        context.commit('setItems', snapshot.val())
      })
    }
  }
})
