<template>
  <tr class="item">
    <td>
      {{ item.name }}
    </td>
    <td>
      {{ item.email }}
    </td>
    <td>
      {{ item.date }}
    </td>
    <td>
      <a @click="edit" class="link"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M3 17.25V21h3.75L17.81 9.94l-3.75-3.75L3 17.25zM20.71 7.04c.39-.39.39-1.02 0-1.41l-2.34-2.34c-.39-.39-1.02-.39-1.41 0l-1.83 1.83 3.75 3.75 1.83-1.83z"/><path d="M0 0h24v24H0z" fill="none"/></svg></a>
    </td>
    <td>
      <a @click="remove" class="link"><svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path d="M6 19c0 1.1.9 2 2 2h8c1.1 0 2-.9 2-2V7H6v12zM19 4h-3.5l-1-1h-5l-1 1H5v2h14V4z"/><path d="M0 0h24v24H0z" fill="none"/></svg></a>
    </td>
  </tr>
</template>

<script>

export default {
  name: 'OneItem',
  props: {
    item: Object,
    index: String,
  },
  data: function() {
    return {
    }
  },
  methods: {
    edit: function() {
      this.$router.push('/edit/'+this.index)
    },
    remove: function() {
      this.$snotify.confirm('Do you want to permanently delete item?', 'Are you sure?', {
      buttons: [
        {text: 'Yes', action: (toast) => {
          this.$store.dispatch('remove', {index: this.index, vm: this})
          this.$snotify.remove(toast.id);

        }, bold: false},
        {text: 'No', action: (toast) => this.$snotify.remove(toast.id)},
      ]});
      },
  }
}
</script>

